import React, { useEffect, useState } from "react";
import { FunnelFill } from "react-bootstrap-icons";
import { Search } from "react-feather";
import ReactMultiSelectCheckboxes from "react-multiselect-checkboxes";

import { useAppDispatch, useAppSelector } from "src/hooks/redux";
import IBrandUserModel from "src/interfaces/Brand/IQueryBrandModel";
import { getBrands } from "../reducer";
import { Link } from "react-router-dom";
import BrandTable from "./BrandTable";
import IQueryBrandModel from "src/interfaces/Brand/IQueryBrandModel";
import ISelectOption from "src/interfaces/ISelectOption";

const ListBrand = () => {
  const dispatch = useAppDispatch();
  const { brands, loading } = useAppSelector((state) => state.brandReducer);

  const [query, setQuery] = useState({
    page: brands?.currentPage ?? 1,
  } as IQueryBrandModel);

  const [search, setSearch] = useState("");

  const [selectedType, setSelectedType] = useState([
    { id: 1, label: "All", value: "All" },
  ] as ISelectOption[]);

  const handleType = (selected: ISelectOption[]) => {
    if (selected.length === 0) {
      /*
      setQuery({
        ...query,
        type: [],
      });

      setSelectedType([UserTypeOptions[0]]);
      return;
      */
    }

    const selectedAll = selected.find((item) => item.id === 1);

    setSelectedType((prevSelected) => {
      if (!prevSelected.some((item) => item.id === 1) && selectedAll) {
        /*
        setQuery({
          ...query,
          type: [],
        });
        */
        return [selectedAll];
      }

      const newSelected = selected.filter((item) => item.id !== 1);
      const type = newSelected.map((item) => item.value as string);
      /*
      setQuery({
        ...query,
        type,
      });
      */
      return newSelected;
    });
  };

  const handleChangeSearch = (e) => {
    e.preventDefault();

    const search = e.target.value;
    setSearch(search);
  };

  const handlePage = (page: number) => {
    setQuery({
      ...query,
      page,
    });
  };

  const handleSearch = () => {
    setQuery({
      ...query,
      search,
    });
  };

  const handleSort = (sortOrder: string) => {
    let orderBy = query.sortOrder === "Accsending" ? "Decsending" : "Accsending";

    if (query.sortOrder !== sortOrder) {
      orderBy = "Decsending";
    }

    if (sortOrder === "No") {
      return;
    }
    /*
    setQuery({
      ...query,
      orderByColumn,
      orderBy,
    });
    */
  };

  const fetchData = () => {
    dispatch(getBrands(query));
  }

  useEffect(() => {
    fetchData();
  }, [query]);

  /*
              <ReactMultiSelectCheckboxes
              options={BrandTypeOptions}
              hideSearch={true}
              placeholderButtonLabel="Type"
              value={selectedType}
              onChange={handleType}
            />
  */
  return (
    <>
      <div className="primaryColor text-title intro-x">Brand List</div>

      <div>
        <div className="d-flex mb-5 intro-x">
          <div className="d-flex align-items-center w-md mr-5">

            <div className="border p-2">
              <FunnelFill />
            </div>
          </div>

          <div className="d-flex align-items-center w-ld ml-auto">
            <div className="input-group">
              <input
                onChange={handleChangeSearch}
                value={search}
                type="text"
                className="form-control"
              />
              <span onClick={handleSearch} className="border p-2 pointer">
                <Search />
              </span>
            </div>
          </div>

          <div className="d-flex align-items-center ml-3">
            <Link to="/user/create" type="button" className="btn btn-danger">
              Create new Brand
            </Link>
          </div>
        </div>

        <BrandTable
          brands={brands}
          handlePage={handlePage}
          handleSort={handleSort}
          sortState={{
            columnValue: query.sortColumn,
            orderBy: query.sortOrder,
          }}
          fetchData={fetchData}
        />
      </div>
    </>
  );
};

export default ListBrand;
